	const handleLoadOptions = (inputValue, callback) => {
        API.getContracts().then(response => {
            if(response && response.error === undefined){
				let searchResult = response.reduce ((result, item) => {
					const search = {
						label: item.name,
						value: item._id
					};

					result.push (search);
					
					return result;
				}, []);

				if (inputValue !== "") {
					searchResult = searchResult.filter(i =>
						i.label.toLowerCase().includes(inputValue.toLowerCase())
					);
				}
				
				callback (searchResult);
            }else{
				console.log('getcontractlist api error: ', response);
            }
        });
	};

	const handleLoadBaseNodesFromAPI = (id: any = '', contract = 'benefit', isRevision = false) => {
		setLoading(true);

        API.getBaseBenefits().then(response => {
			if(response && response !== undefined && response.message === undefined
				&& response.error === undefined){
				const data: any[] = [...response];
				let nodes : any[] = [];
				let eList : any[] = [];

				nodes = convertNodeData(data);
				
				if (id !== '') {
					handleLoadBenefitNodesFromAPI(nodes, eList, id, isRevision);
				} else {
					setLoading(false);

					nodes = filterInitialNodes(nodes, contract);
					eList = createExclusionList(nodes);
					SetExclusionList(eList);
					SetSelectedPlanType('BASE');
					SetSelectedPlanPackage('RB');
					SetSelectedBenefitCategory('RBC');
					SetBenefitNodes (nodes);
				}
            }else{
				setLoading(false);
				setModalComponentOpen(true);
				setModalComponentTitle("Error");
				setModalComponentMessage("Please check your internet connection and try again.");
				setModalComponentAction("get_benefit");
				setModalComponentButton(
					<div>
						<Button
							onClick={()=>{
								handleRetryModal("back_benefit");
							}}
							className={classes.backButton}
							variant="contained"
							color="secondary"
						>
							Back
						</Button>
						<Button
							className={classes.retryButton}
							onClick={() => {
								handleRetryModal("get_benefit")
							}}
							variant="contained"
							color="primary"
						>
							Retry
						</Button>
					</div>
				)
				console.log('getbenefitlist api error: ', response);
            }
        });
	}

	const handleLoadBenefitNodesFromAPI = (baseNodesParam: any, exclusionListParam: any, id: number, isRevision = false) => {
		API.getBenefitsTree(id).then(response => {
            if(response && response.error === undefined){
				const data: any[] = [...response];
				let baseNodes: any [] = [...baseNodesParam];
				let benefitNodes : any[] = [];
				let eList : any[] = [...exclusionListParam];

				benefitNodes = convertNodeData(data, true);
				//console.log('load benefit nodes: ',benefitNodes)
				let MCTNode:any[] = benefitNodes.filter(node => 
                    (node['code'] && 
					node['code'] === "MCT"))

                if (MCTNode !== undefined && MCTNode[0].comments) {
                    let comments = MCTNode[0].comments;

                    setCommentsLoaded(true);
					setCommentAction(comments['action'] ? comments['action'] : '')
					setCommentApprover(comments['full_name']);
                    // setCommentApprover(comments['first_name'] + ' ' + comments['last_name']);
                    setCommentContent(comments['content'] ? comments['content'] : '');
				}

                const adddtls:any = [];
                let PInfo:any = {}
                if(MCTNode !== undefined && MCTNode[0].custom_metadata.additional_info)
                {if(MCTNode[0].custom_metadata.additional_info.contact_person){
                    adddtls.push('Contact Person')
                }
                if(MCTNode !== undefined && MCTNode[0].custom_metadata.additional_info.company_name){
                    adddtls.push('Company Name')
                }
                if(MCTNode !== undefined && MCTNode[0].custom_metadata.additional_info.address){
                    adddtls.push('Address') 
                }
                PInfo ={
                    coverage_start_date:MCTNode[0].custom_metadata.coverage_start_date,
                    coverage_end_date:MCTNode[0].custom_metadata.coverage_end_date,
                    additional_details:adddtls,
                    CompanyNameSelected: MCTNode[0].custom_metadata.additional_info.company_name ? MCTNode[0].custom_metadata.additional_info.company_name : '',
					ContactPersonSelected: MCTNode[0].custom_metadata.additional_info.contact_person ? MCTNode[0].custom_metadata.additional_info.contact_person : '',
					ContactPersonName: MCTNode[0].custom_metadata.additional_info.contact_person_name ? MCTNode[0].custom_metadata.additional_info.contact_person_name : '',
					ContactPersonNumber: MCTNode[0].custom_metadata.additional_info.contact_person_number ? MCTNode[0].custom_metadata.additional_info.contact_person_number : '',
					ContactPersonEmailAddress: MCTNode[0].custom_metadata.additional_info.contact_person_email_address ? MCTNode[0].custom_metadata.additional_info.contact_person_email_address : '',
					ContactPersonDesignation: MCTNode[0].custom_metadata.additional_info.contact_person_designation ? MCTNode[0].custom_metadata.additional_info.contact_person_designation : '',
                    AddressSelected:MCTNode[0].custom_metadata.additional_info.address && MCTNode[0].custom_metadata.additional_info.address==='branch' ? 'branch' : 'Head Office',
                    SelectedBranch:MCTNode[0].custom_metadata.additional_info.address && MCTNode[0].custom_metadata.additional_info.address==='branch' ? MCTNode[0].custom_metadata.additional_info.address : '',
                }}
                else
                {
                    PInfo ={
                        coverage_start_date:MCTNode[0].custom_metadata.coverage_start_date,
                        coverage_end_date:MCTNode[0].custom_metadata.coverage_end_date,
                    }
                }
                SetProposalInfo(Object.assign(proposalInfo, PInfo));
				let nodes: any[] = combineBaseBenefitNodes(baseNodes, benefitNodes, isRevision);
				for(const i in nodes){
					if(nodes[i] && nodes[i] !== undefined &&
						nodes[i]["code"] === "CT"){
						if(nodes[i]['uploadedComputation'] && nodes[i]['uploadedComputation'] !== undefined){
							let newComputation = nodes[i]['uploadedComputation'].reduce((result, file) => {
								let newFile = Object.assign({}, file);
								newFile['isUploaded'] = true;
								result.push(newFile);
								return result;
							}, new Array());
							nodes[i]['uploadedComputation'] = newComputation;
						}
						break;
					}
				}
				//console.log('combine base nodes: ',nodes)
				eList = createExclusionList(nodes);
				SetContractType('benefit');
				setLoading(false);
				SetExclusionList(eList);
				// SetEditedNodeList([]);
				SetSelectedPlanType('BASE');
				SetSelectedPlanPackage('RB');
				SetSelectedBenefitCategory('RBC');
				SetBenefitNodes ([...nodes]);
				setInitialNodes([...nodes]);
            }else{
				setLoading(false);
				console.log('getbenefitlist api error: ', response);
            }
        });
	}

