	const getNodesPayload = (nodeData: any, clientData: any, proposalInfo: any, isRevision:boolean = false) => {
		// Filter out benefits that aren't included in base plan
		// Making several filter loops (backwards from sub-benefit to plan package) to make sure
		// children are accounted for
		let nodes = [...nodeData];

		for (let x = 8; x > 3; x--) {
			nodes = nodes.filter ((item: any) => GlobalFunction.filterNodesByLevel(item, nodes, x));
			
			nodes = nodes.map ((item: any) => {
				let newItem = Object.assign({}, item);

				newItem.children = newItem.children.filter(child =>
		          nodes.find((node) => node.id === child) ? true : false
		        );
		        return newItem;
			});
		}
		// console.log("filteredNodes1:", JSON.stringify(nodes));

		nodes = nodes.map ((item: any) => {
			let newItem = Object.assign({}, item);

			newItem.children = newItem.children.filter(child =>
	          nodes.find((node) => node.id === child) ? true : false
	        );
			//Add custom meta data for MCT
			if(newItem.code === 'MCT'){
				
				if (!newItem['custom_metadata']) {
					newItem['custom_metadata'] = {};
				}
				if (!newItem['custom_metadata_schema']) {
					newItem['custom_metadata_schema'] = {};
					newItem['custom_metadata_schema']["properties"] = {};
				}
				newItem['custom_metadata']['created_by'] = localStorage['user_id']
				newItem['custom_metadata_schema']["properties"]["created_by"] = {
					"type": "string"
				}
				if(newItem['custom_metadata']['version'] && isRevision){
					const num = parseInt(newItem['custom_metadata']["version"].replace(/[^0-9]/g,''));

					if (!isNaN(num)) {
						newItem['custom_metadata']["version"] = "V" + (num+1);
					} else {
						newItem['custom_metadata']["version"] = "V1";
					}
				} else {
					newItem['custom_metadata']['version'] = "V1"
					newItem['custom_metadata_schema']["properties"]["version"] = {
						"type": "string"
					}
				}

				if(!newItem['custom_metadata']['date_created']){
					// newItem['custom_metadata']['date_created'] = moment(new Date()).format('MM/DD/YYYY');
					// newItem['custom_metadata_schema']["properties"]["date_created"] = {
					// 	"type": "string"
					// }
					newItem['custom_metadata']['date_created'] = new Date();
				}
				if(newItem['custom_metadata']['coverage_start_date']){
					newItem['custom_metadata']['coverage_start_date'] = moment(proposalInfo.coverage_start_date).format("YYYY-MM-DDT00:00:00.000") + "Z";
				}
				if(newItem['custom_metadata']['coverage_end_date']){
					newItem['custom_metadata']['coverage_end_date'] = moment(proposalInfo.coverage_end_date).format("YYYY-MM-DDT23:59:59.000") + "Z";
					
				}
				if(proposalInfo.additional_details.includes("Address") || 
					proposalInfo.additional_details.includes("Contact Person") || 
					proposalInfo.additional_details.includes("Company Name") ){
						let addinfo = {};
						newItem['custom_metadata_schema']["properties"]["additional_info"]={}
						if(proposalInfo.additional_details.includes("Address"))
						{
							if(proposalInfo.AddressSelected==='branch')
							{addinfo['address']=proposalInfo.SelectedBranch}
							else{
								addinfo['address']=proposalInfo.AddressSelected
							}
							newItem['custom_metadata_schema']["properties"]["additional_info"]["address"] = {
								"type": "string"
							}
						}
						if(proposalInfo.additional_details.includes("Contact Person")){
							addinfo['contact_person'] = proposalInfo.ContactPersonSelected
							if(proposalInfo.ContactPersonSelected !== 'Signatory'){
								addinfo['contact_person_name'] = proposalInfo.ContactPersonName
								newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_name"] = {
									"type": "string"
								}
								if(proposalInfo.ContactPersonNumber && proposalInfo.ContactPersonNumber.trim() !== ''){
									addinfo['contact_person_number'] = proposalInfo.ContactPersonNumber
									newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_number"] = {
										"type": "string"
									}
								}
								if(proposalInfo.ContactPersonEmailAddress && proposalInfo.ContactPersonEmailAddress.trim() !== ''){
									addinfo['contact_person_email_address'] = proposalInfo.ContactPersonEmailAddress
									newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_email_address"] = {
										"type": "string"
									}
								}
								if(proposalInfo.ContactPersonDesignation && proposalInfo.ContactPersonDesignation.trim() !== ''){
									addinfo['contact_person_designation'] = proposalInfo.ContactPersonDesignation
									newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_designation"] = {
										"type": "string"
									}
								}
							}
							newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person"] = {
								"type": "string"
							}
						}
						if(proposalInfo.additional_details.includes("Company Name")){
							addinfo['company_name'] = proposalInfo.CompanyNameSelected
							newItem['custom_metadata_schema']["properties"]["additional_info"]["company_name"] = {
								"type": "string"
							}
						}
						newItem['custom_metadata']['additional_info']=addinfo;
				}
				if(!newItem['custom_metadata']['date_updated']){
					// newItem['custom_metadata']['date_updated'] = ""
					// newItem['custom_metadata_schema']["properties"]["date_updated"] = {
					// 	"type": "string"
					// }
					newItem['custom_metadata']['date_updated'] = "";
				}
				
				newItem["status"] = isRevision ? STATUS_REVISED_PROPOSAL : STATUS_SUBMIT_PROPOSAL;

				if (!newItem["contract_type"]) {
					newItem["contract_type"] = contractType ? contractType : 'benefit';
				}
				
				if (newItem["version"] && isRevision) {
					const num = parseInt(newItem["version"].replace(/[^0-9]/g,''));

					if (!isNaN(num)) {
						newItem["version"] = "V" + (num+1);
						newItem["proposal_number"] = "V" + (num+1);
					} else {
						newItem["version"] = "V1";
						newItem["proposal_number"] = "V1";
					}

				} else {
					newItem["version"] = "V1";
					newItem["proposal_number"] = "V1";
				}

				/*if (newItem["proposal_number"] && isRevision) {
					const num = parseInt(newItem["version"].replace(/[^0-9]/g,''));

					if (!isNaN(num)) {
						newItem["proposal_number"] = "V" + (num+1);
					} else {
						newItem["proposal_number"] = "V1";
					}

				} else {
					newItem["proposal_number"] = "V1";
				}*/
			}
	        if (newItem.code === 'CT') {
	        	newItem['principals_count'] = parseInt(clientData.principalData.numOfPrincipals);
	        	newItem['male_principals_count'] = parseInt(clientData.principalData.malePrincipals);
	        	newItem['female_principals_count'] = parseInt(clientData.principalData.femalePrincipals);
	        	newItem['dependents_count'] = parseInt(clientData.principalData.numOfDependents);
	        	newItem['male_dependents_count'] = parseInt(clientData.principalData.maleDependents);
	        	newItem['female_dependents_count'] = parseInt(clientData.principalData.femaleDependents);
	        }

	        if (newItem.code === 'OD' || newItem.code === 'CB') {
	        	const networkNode = nodes.find((node) =>  {
					return 	node.name === "Network" &&
				            node.code === "NT" &&
				            node.level === 4 && 
				            newItem.tree_path[2] === node.tree_path[2];
	        	});

	        	if (networkNode['custom_metadata'] && networkNode['custom_metadata']['type'] === newItem.code) {

	        		let metadata = Object.assign({}, newItem['custom_metadata']);

					if (metadata['partner_network_list'] && metadata['partner_network_list'].length > 0) {

						const newPartnerNetworkList = [...metadata['partner_network_list']];

						let includedFacilities: any[] = [];
						let excludedFacilities: any[] = [];

						metadata['partner_network_list'] = newPartnerNetworkList.map((partner: any) => {
							let newPartner: any = Object.assign({}, partner);

							if (newPartner['included'] !== undefined) {
								if (newPartner['included'] === true) {
									delete newPartner.included;
									delete newPartner.parent;
									delete newPartner.index;
									includedFacilities.push(newPartner);
								} else if (newPartner['included'] === false) {
									delete newPartner.included;
									delete newPartner.parent;
									delete newPartner.index;
									excludedFacilities.push(newPartner);
								}
							}
						});

						delete metadata.partner_network_list;

						metadata['included_facilities'] = includedFacilities;
						metadata['excluded_facilties'] = excludedFacilities;

						newItem['custom_metadata'] = metadata;
					}
				}
			}

			delete newItem.tree_path;
			delete newItem.tree_id;
			delete newItem.included;
			delete newItem.count;
			delete newItem.level;
			delete newItem.edited;
			delete newItem.node_path;
			delete newItem.original_node;
			delete newItem.original_children;
			delete newItem.original_id;
			delete newItem.deleted;
			delete newItem.is_base;
			delete newItem.disabled;
			delete newItem.default_limits;
			return newItem;
		});
		// console.log("filteredNodes2:", JSON.stringify(nodes));
		return nodes;
	}

