	const getFiles = (files: any, uploadedFlag: boolean) => {
		files = [
			...props.clientData.businessData.uploadedLogo ?
				props.clientData.businessData.uploadedLogo
				.filter(file => file 
					&& ((uploadedFlag === false && (!file.isUploaded || file.isUploaded === undefined)
						&& file.name !== undefined) 
					|| (uploadedFlag === true && file.isUploaded)))
				.map(file => {
					return {
						name: file.name,
						type: file.type,
						file: file,
						field: 'icon'
					}
				}) : [],
			...props.clientData.HMOData.uploadedUtilization ?
				props.clientData.HMOData.uploadedUtilization
				.filter(file => file
					&& ((uploadedFlag === false && (!file.isUploaded || file.isUploaded === undefined)
						&& file.name !== undefined ) 
					|| (uploadedFlag === true && file.isUploaded)))
				.map(file => {
					return {
						name: file.name,
						type: file.type,
						file: file,
						field: 'utilization'
					}
				}) : [],
			...props.contractNode && props.contractNode.uploadedComputation ?
				props.contractNode.uploadedComputation
				.filter(file => file
					&& ((uploadedFlag === false && (!file.isUploaded || file.isUploaded === undefined)
						&& file.name !== undefined) 
					|| (uploadedFlag === true && file.isUploaded)))
				.map(file => {
					return {
						name: file.name,
						type: file.type,
						file: file,
						field: 'computation'
					}
				}) : []
		]
		return files;
	}

