	const routeWorkflow = (proposal: any, ticket_id: string) => {
		if(proposal !== undefined 
			&& proposal.client_id !== undefined && proposal.mother_contract_id){
			Utils.StorageService('user_data').then((result)=>{
				let underwriting_staff_flag: boolean = false;
				if(result && result !== undefined){
					for(const i in result){
						if(result[i]["key"] === "group_name"){
							if(result[i]["value"] && result[i]["value"] !== undefined
							&& result[i]["value"].includes("UNDERWRITING_STAFF")){
								underwriting_staff_flag = true;
							}
							break;
						}
					}
				}
				const variables = {
					"is_sme_account": false,
					"mother_contract_id": proposal.mother_contract_id,
					"client_id": proposal.client_id,
					"client_name": ""
				}
				if(proposal.client_name !== undefined){
					variables["client_name"] = proposal.client_name;
				}
				if(contractType === "sme" || underwriting_staff_flag === true){
					variables["is_sme_account"] = true;
					variables["use_sme_plan"] = true;
				}else{
					variables["customize_standard_benefit_plan"] = true;
				}
				console.log('route work flow function',variables)
				Processmaker.put('cases/'+ticket_id+'/variable','',variables)
				.then(response => {
					console.log(response);
					Processmaker.put('cases/'+ticket_id+'/route-case','',{})
					.then(response => {
						console.log(response);
						Processmaker.put('cases/'+ticket_id+'/route-case','',{})
						.then(response => {
							console.log(response);
							Processmaker.put('cases/'+ticket_id+'/route-case','',{})
							.then(response => {
								console.log(response);
								if(contractType === "sme" || underwriting_staff_flag === true){
									Processmaker.put('cases/'+ticket_id+'/route-case','',{})
									.then(response => {
										console.log(response);
										if(underwriting_staff_flag === true){
											setRedirectPath('/underwriting/finalize-proposal/' +
													proposal.client_id +
												'/' +
													proposal.mother_contract_id +
												'/uploader/' +
													ticket_id)
											setRedirectFlag(true);
										}else{
											successRedirectToDashboard();
										}
									})
								}else{
									successRedirectToDashboard();
								}
							});
						});
					});                 
				});
			})
		}else{
			setTimeout(() => {
				successRedirectToDashboard();
			}, 1500);
		}
	}

