import React from 'react';

import { useState, useEffect }  from 'react';
import Typography from '@material-ui/core/Typography';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

import { IApproveProposal } from 'Pages/ApproveProposal/IApproveProposal';

import {
	Grid,
} from '@material-ui/core';

import { 
	LimitTypeItems,
	EligibleAvaileesItems,
	MaternityEligibleAvaileesItems,
	EligibilityItems,
	MemberTypeItems
} from 'Components/UI/TableComponent/DropdownConstants';

interface SummaryProps {
	classes: any;
}

const AvaileeItems = [...EligibleAvaileesItems, ...MaternityEligibleAvaileesItems, ...EligibilityItems, ...MemberTypeItems];

export const Summary:React.FC<IApproveProposal.IProps & SummaryProps> = (props: IApproveProposal.IProps & SummaryProps) => {
	const [selectedCode, setselectedCode] = useState('');
	const { classes, 
			summaryPackages, 
			summaryBenefits, 
			selectedSummaryCategory,
			SetSelectedSummaryPackage, 
			SetSelectedSummaryCategory 
		} = props;

	useEffect(() => {
		if (summaryPackages.length > 0) {
			const first = summaryPackages[0];
			SetSelectedSummaryPackage(`${first.tree_path[3]}`);
			SetSelectedSummaryCategory(`${first.code}`);
			setselectedCode(first.code);
		}
	}, []);


	const displayColumnEligibility = ["APE","PPE","MT","EC","SH","ADD"].includes(selectedSummaryCategory);
	let displayColumnContractPlanType = false;

	let displayColumnEligibilityName = 'eligible_availees';

	if (["EC"].includes(selectedSummaryCategory)) {
		displayColumnEligibilityName = 'eligibility';
	} else if (["SH","ADD"].includes(selectedSummaryCategory)) {
		displayColumnEligibilityName = 'member_type';
	}

	summaryBenefits.map ((planType) => {
		if (planType['contract_plan_type']) {
			displayColumnContractPlanType = true;
		}
	});

	return (
			<Grid container className={classes.gridContainer}>
				<Grid item xs={12}>
					<Typography className={classes.sectionTitle}>
						Summary Per Plan Type
					</Typography>
			        <Tabs value={selectedSummaryCategory} variant='scrollable' className={classes.tabs}>
			         {
			         	summaryPackages.length > 0 ? 
			         		summaryPackages.map((item: any, i: number) => 
			         			 <Tab 
			         			 	key={i}
			         			 	value={item.code}
			         			 	label={item.name}
			         			 	className={classes.tab}
									onClick={() => {
										SetSelectedSummaryPackage(`${item.tree_path[3]}`);
										SetSelectedSummaryCategory(`${item.code}`);
										setselectedCode(item.code);
									}}
			         			 />
							) : null
			         }
			        </Tabs>

				    <Table className={classes.table}>
				        <TableHead>
				          	<TableRow>
					            <TableCell className={classes.columnCategory}>Plan Type</TableCell> 
					            {
					            	displayColumnContractPlanType ? 
					            		<TableCell className={classes.columnCategory}>Contract Plan Type</TableCell>  
					            		: null
					            }
					            <TableCell className={classes.columnBenefit}>Benefit</TableCell>
					           	<TableCell className={classes.columnLimit}>Limit</TableCell>
					           	{
					           		displayColumnEligibility ? 
					           			<TableCell className={classes.columnEligibility}>Eligible Members</TableCell> 
					           			: null
					           	}
					           	
					    	{/*     
					    		<TableCell>Effectivity</TableCell>
					            <TableCell>Reimbursement</TableCell>
					    	*/}
				          	</TableRow>
				        </TableHead>
				        <TableBody>
				        	{
				        		summaryBenefits.length > 0 ? 
				        			summaryBenefits.map((planType, i) => 
				        				planType['child_nodes'] && planType['child_nodes'].length > 0 ? 
				        					planType['child_nodes'].map ((row, x) => {

				        						let eligibility: any = null;

				        						if (row['custom_metadata'] && row['custom_metadata'][displayColumnEligibilityName] && 
												    row['custom_metadata'][displayColumnEligibilityName].length > 0) {

				        							if (displayColumnEligibilityName !== 'eligibility') {
				        								eligibility = row['custom_metadata'][displayColumnEligibilityName].map((availee: any, i: number) => {
								              				const found:any = AvaileeItems.find((item: any) => item.value === availee);

								              				if (found) {
								              					return (
																	<React.Fragment key={i}>
																		{found.label}
																		<br /> 
																	</React.Fragment>
								              					);
								              				}
								              				return null;
								              			});
				        							} else {
				        								const found:any = AvaileeItems.find((item: any) => item.value === row['custom_metadata'][displayColumnEligibilityName]);

							              				if (found) {
							              					eligibility = <React.Fragment key={i}>
																	{found.label}
																	<br /> 
																</React.Fragment>;
							              				}
				        							}
				        						}
												          
				        						return (
													<TableRow className={i % 2 ? classes.tableRowEven : classes.tableRowOdd} key={x}>
										              { 
										              	x === 0 ? 
										              		<TableCell component="th" scope="row" rowSpan={planType['child_nodes'].length}>
												            	{planType['name'] ? planType['name'] : ''}
												            </TableCell> : null

										              }   
										              { 
										              	displayColumnContractPlanType && x === 0 ? 
										              		<TableCell component="th" scope="row" rowSpan={planType['child_nodes'].length}>
												            	{planType['contract_plan_type'] ? planType['contract_plan_type'] : ''}
												            </TableCell> : null

										              }     
										              <TableCell>
										              	{row.name}
										              </TableCell>
										              <TableCell>
														  { selectedCode === "RB" ?
																props.benefitNodes && props.benefitNodes.length > 0 ?
																	props.benefitNodes.map((node: any) => {
																		if (node.code === "RB" && node.tree_path[2] &&
																			planType.name && node.tree_path[2].toLowerCase() === planType.name.replace(/\s+/g, '').toLowerCase()
																			&& node.limits && node.limits.length > 0){
																				const limits = node.limits.map((lim: any, i: number) => {
																					const found = LimitTypeItems.find ((item) => item.type === lim.type);
																					let name = '';
																					let suffix = '';
																					let value = lim.limit;
																					
																					if (found) {
																						name = found.label;
																					}

																					if (lim.limit_unit === 'PHP') {
																						suffix = 'PHP';
																						value = GlobalFunction.formatNumber(value);
																					}

																					if (lim.limit_unit === 'Percentage' || ['RVS','% of MBL','% of ABL'].includes(lim.type)) {
																						suffix = '%';

																						if (['ABL', 'MBL'].includes(lim.type)) {
																							suffix = '% ' + lim.type;
																						}
																					}

																					if (lim.limit_unit === 'Sessions') {
																						suffix = lim.limit == 1 ? 'session' : 'sessions';
																					}

																					if (lim.limit_unit === 'Visits') {
																						suffix = lim.limit == 1 ? 'visit' : 'visits';
																					}
																					if ((['ABL', 'MBL'].includes(lim.type))) {
																						return (
																							<React.Fragment key={i}>
																								{name}{value ? ": " : ''}{value} {suffix}
																								<br /> 
																							</React.Fragment>
																						);
																					}else{
																						return null;
																					}
																				})
																				let fpeclimit = node.limits.find(lim => {
																					return lim.type === 'PEC';
																				});
	
																				if(fpeclimit){
																					let peclimits: any[] = [];
																					if(fpeclimit.limit_unit === 'Months'){
																						let tblimits = fpeclimit.time_based_limit ? fpeclimit.time_based_limit : [];
																						if(tblimits.length > 0){
																							tblimits.forEach((lim, index) => {
																								const nm = lim.number_month ? lim.number_month : 0;
																								let day_base = (nm === 1 ? 'Month' : 'Months');
																								switch(lim.day_based){
																									case 'Weeks':
																										day_base = (nm === 1 ? 'Week' : 'Weeks');
																										break;
																									case 'Days':
																										day_base = (nm === 1 ? 'Day' : 'Days');
																										break;
																									default:
																										break;
																								}
																								const val = lim.limit ? lim.limit : 0;
																								const unit = lim.limit_type === 'percent' ? '%' : ' '+lim.limit_type;
																								const comma = index < (tblimits.length-1) ? ', ' : '';
																								peclimits.push(
																									<React.Fragment>
																										{index === 0 ? 'PEC: ' : <span style={{paddingLeft: 32}} />}After {nm} {day_base} - {val}{unit}{comma}<br />
																									</React.Fragment>
																								);
																							});
																						}
																					}
																					else{
																						let suffix = '';
																						let value = fpeclimit.limit;
																						
																						if (fpeclimit.limit_unit === 'PHP') {
																							suffix = 'PHP';
																							value = GlobalFunction.formatNumber(value);
																						}
	
																						if (fpeclimit.limit_unit === 'Percentage') {
																							suffix = '%';
																						}
	
																						if (fpeclimit.limit_unit === 'Sessions') {
																							suffix = fpeclimit.limit === 1 ? 'session' : 'sessions';
																						}
	
																						if (fpeclimit.limit_unit === 'Visits') {
																							suffix = fpeclimit.limit === 1 ? 'visit' : 'visits';
																						}
																						
																						peclimits.push(
																							<React.Fragment>
																								{'PEC: '} {value} {suffix}
																							</React.Fragment>
																						);
																					}
																					
																					if(peclimits.length > 0){
																						limits.push(
																							<React.Fragment>
																								{peclimits} 
																							</React.Fragment>
																						);
																					}
																				}
																			return limits;
																		}else{
																			return null;
																		}
																	})
																:null
															: null
														}
														{ selectedCode !== "RB" && row.limits && row.limits.length > 0 ?
																row.limits.map((lim: any, i: number) => {
																	const found = LimitTypeItems.find ((item) => item.type === lim.type && item.limit_unit === lim.limit_unit);
																	let name = '';
																	let suffix = '';
																	let value = lim.limit;
																	
																	if (found) {
																		name = found.label + ": ";
																	}

																	if (lim.limit_unit === 'PHP') {
																		suffix = 'PHP';
																		value = GlobalFunction.formatNumber(value);
																	}

																	if (lim.limit_unit === 'Percentage') {
																		suffix = '%';

																		if (['ABL', 'MBL'].includes(lim.type)) {
																			suffix = '% ' + lim.type;
																		}
																	}

																	if (lim.limit_unit === 'Sessions') {
																		suffix = lim.limit == 1 ? 'session' : 'sessions';
																	}

																	if (lim.limit_unit === 'Visits') {
																		suffix = lim.limit == 1 ? 'visit' : 'visits';
																	}
																	return (
																		<React.Fragment key={i}>
																			{name}{value} {suffix}
																			<br /> 
																		</React.Fragment>
																	);
																}) : null
														}
										              </TableCell>
											           	{
											           		displayColumnEligibility ? <TableCell>{eligibility}</TableCell> : null
											           	}
										            </TableRow>


				        						);
					        				}
				        				) : null
				        			) : 
						          	<TableRow>
							            <TableCell colSpan={4}>No additional benefits on top of Base Plan have been added.</TableCell>
						          	</TableRow>
				        	}
				        </TableBody>
				     </Table>
				</Grid>
			</Grid>
	);
};