interface MemberFieldsSectionProps {
	memberFieldsData: any[];
	clientRequiredFields: any[];
	clientAddedFields: number;
	mandatoryFields: any[];
	lastSortOrder: number;
	//classes: any;
	setData(memberFieldsData: any[]): void;
	invalidFields: string[] | null;
	viewOnly?: boolean;
	kycEditable?: boolean;
}


const MemberFieldsSection = (props: MemberFieldsSectionProps) => {
	//const classes = props.classes;

	const [isDrawerOpen, setIsDrawerOpen] = useState(false);
	const [limit] = useState(5);
	const [tabs] = useState(['Client-required', 'Mandatory']);
	const [selectedTab, setSelectedTab] = useState(0);
	const [snackbarOpen, setSnackbarOpen] = useState(false)
	const [snackbarMsg, setSnackBarMsg] = useState<any>(null)
	//const [memberFieldsData, setMemberFieldsData] = useState<any>([]);
	const [mssgOpen, setMssgOpen] = useState(false)
	const [invalidFields, setInvalidFields] = useState<string[]>([]);
	const handleTabChange = (
		_event: React.ChangeEvent<HTMLDivElement>,
		tab: number,
	) => {
		setSelectedTab(tab);
	};

	const onAddDataFieldBtnClicked = () => {
		if (props.clientAddedFields >= limit) {
			setMssgOpen(true);
		} else {
			setIsDrawerOpen(true);
		}
	};
	const getNextSortOrder = () => {
		const nextSortOrder = props.lastSortOrder + 1;
		//setLastSortOrder(nextSortOrder);

		return nextSortOrder;
	};
	const onDrawerSubmit = (data: any) => {
		if (!isValidMemberFieldData(data)) {
			return;
		}

		addToMemberFieldsData(data);
		setIsDrawerOpen(false);
	};

	const onDrawerClose = () => {
		setIsDrawerOpen(false);
	};



	const addToMemberFieldsData = (data: any) => {
		let memberFieldsDataCopy = cloneDeep(props.memberFieldsData);
		const newMemberField = {
			system_name: data.fieldName,
			field_name: snakeCase(data.fieldName),
			is_required: data.fieldIsRequired,
			input_type: data.fieldInputType,
			data_type: data.fieldDataType,
			info_type: 'Member Profile',
			field_type: 'Client-required',
			field_choices: data.fieldDropdownValues,
			is_client_added: true,
			sort_order: getNextSortOrder(),
		};
		memberFieldsDataCopy.push(newMemberField);
		if (props.setData) {
			props.setData(memberFieldsDataCopy);
		}
	};

	const isValidMemberFieldData = (data: any) => {
		const {
			fieldName,
			fieldInputType,
			fieldDataType,
			fieldDropdownValues,
		} = data;

		let msg: string[] = [];
		if (fieldName === '') {
			msg.push('Empty field name')
		}

		if (fieldInputType === '') {
			msg.push('Input type not selected')
		}

		if (fieldInputType === 'Text Field' && fieldDataType === '') {
			msg.push('Data type not selected')
		}

		if (fieldInputType === 'Dropdown' && fieldDropdownValues.length === 0) {
			msg.push('No dropdown options')
		}
		if (msg.length > 0) {
			setInvalidFields(msg);
			setSnackBarMsg(msg.map((items) => { return (<div>{items}</div>) }))
			setSnackbarOpen(true)
			return false;
		} else {
			return true;
		}
	};

	const handleIsRequiredClick = (rowData: any) => {
		const {
			data: { row: changedData },
		} = rowData;

		if (changedData) {
			const memberFieldsDataCopy = cloneDeep(props.memberFieldsData);
			forEach(memberFieldsDataCopy, (memberField, idx) => {
				if (
					memberField.system_name === changedData.system_name &&
					memberField.field_name === changedData.field_name
				) {
					memberField.is_required = !memberField.is_required;
					memberFieldsDataCopy[idx] = memberField;
				}
			});
			console.log('check click', memberFieldsDataCopy)
			if (props.setData) {
				props.setData(memberFieldsDataCopy);
			}
		}
	};

	const handleMssgModalClose = () => {
		setMssgOpen(false);
	}
	return (
		<div>
			<Grid container>
				<Grid item xs={8}>
					<Tabs
						value={selectedTab}
						onChange={handleTabChange}
						aria-label="simple tabs example"
					>
						{tabs.map((tab, idx) => (
							<Tab
								id={'verify-member-tab-' + idx}
								label={tab}
								key={'verify-member-tab-' + idx}
								aria-controls={'verify-member-tabpanel-' + idx}
							/>
						))}
					</Tabs>
				</Grid>
				<Grid item xs={4} style={{ paddingTop: '8px' }}>
					<Grid container justify="flex-end" alignItems="flex-end">
						{selectedTab === 0 && !props.viewOnly ?
							<Button
								onClick={onAddDataFieldBtnClicked}
								variant="text"
								color="primary"
							>
								<FontAwesomeIcon icon={faPlus} style={{
									color: '#0D5E40',
									cursor: 'pointer',
									fontSize: '13px',
									marginRight: '8px',
								}} />
								Add a Custom Data Field
              </Button>
							: null}

					</Grid>
				</Grid>
				<Grid item xs={12} style={{ paddingTop: '8px' }}>
					{selectedTab === 0 ? (
						<TableComponent
							id="tab-partial"
							rows={
								Object.keys(props.clientRequiredFields).length > 0
									? (!props.viewOnly || props.kycEditable) ? props.clientRequiredFields['rows']
										: props.clientRequiredFields['rows'].filter(function (element) {
											return (element['is_required']);
										})
									: []
							}
							columns={
								Object.keys(props.clientRequiredFields).length > 0
									? props.clientRequiredFields['columns']
									: []
							}
							message="No available data"
							showSelectionColumn={false}
							onClickRow={(_row: number[]) => { }}
							onClickCell={handleIsRequiredClick}
							disableSelect
							disableSearch
							disableFilter
							disableSort
							disablePaging
							singleSelect={true}
							formattedColumns={
								Object.keys(props.clientRequiredFields).length > 0
									? props.clientRequiredFields['formattedColumns']
									: {}
							}
							columnExtensions={
								Object.keys(props.clientRequiredFields).length > 0
									? props.clientRequiredFields['columnExtensions']
									: []
							}
						/>
					) : selectedTab === 1 ? (
						<TableComponent
							id="tab-partial"
							rows={
								Object.keys(props.mandatoryFields).length > 0
									? (!props.viewOnly || props.kycEditable) ? props.mandatoryFields['rows']
										: props.mandatoryFields['rows'].filter(function (element) {
											return (element['is_required']);
										})
									: []
							}
							columns={
								Object.keys(props.mandatoryFields).length > 0
									? props.mandatoryFields['columns']
									: []
							}
							message="No available data"
							showSelectionColumn={false}
							onClickRow={(_row: number[]) => { }}
							onClickCell={handleIsRequiredClick}
							disableSelect
							disableSearch
							disableFilter
							disableSort
							disablePaging
							singleSelect={true}
							formattedColumns={
								Object.keys(props.mandatoryFields).length > 0
									? props.mandatoryFields['formattedColumns']
									: {}
							}
							columnExtensions={
								Object.keys(props.mandatoryFields).length > 0
									? props.mandatoryFields['columnExtensions']
									: []
							}
						/>
					) : null}
				</Grid>
				<Grid item xs={12} style={{ overflowY: 'auto', maxHeight: '100vh' }}>
					<AddMemberFieldDrawer
						invalidFields={invalidFields}
						isOpen={isDrawerOpen}
						clientRows={Object.keys(props.clientRequiredFields).length > 0
							? (!props.viewOnly || props.kycEditable) ? props.clientRequiredFields['rows']
								: props.clientRequiredFields['rows'].filter(function (element) {
									return (element['is_required']);
								})
							: []}
						onSubmit={onDrawerSubmit}
						onClose={onDrawerClose}
					/>
				</Grid>
				<Snackbar
					key="close"
					anchorOrigin={{
						horizontal: 'right',
						vertical: 'top',
					}}
					open={snackbarOpen}
					onClose={() => {
						setSnackbarOpen(false);
					}}
					autoHideDuration={5000}
				>
					<SnackbarContent
						style={{ backgroundColor: 'rgba(0, 0, 0, 0.75)', position: 'relative', width: '100%', maxHeight: '82vh', overflowY: 'auto' }}
						message={<div>{snackbarMsg}</div>}
						action={
							<div style={{ display: 'inline-block', height: '100%', width: '48px' }}>
								<IconButton
									style={{ position: 'absolute', top: '0' }}
									onClick={() => {
										setSnackbarOpen(false);
									}}
								>
									<CloseIcon style={{ color: 'white' }} />
								</IconButton>
							</div>

						}
					/>
				</Snackbar>
				<MsgModal
					onClose={handleMssgModalClose}
					title='Limit Reached'
					isModalOpen={mssgOpen}
					message='Maximum Number of Custom Data Fields Reached'
				/>
			</Grid>
		</div>
	);
};


