	const handleApproveProposal = (withconditions: boolean, comment: string) => {
		if (!window.navigator.onLine) {
			showHideApprovalModal(false);
			setMssgAction('proposal_click');
			setMssgTitle('Error');
			setMssgBody("Please check your internet connection and try again.");
			setMssgOpen(true);
			return;
		}
		if (withconditions && (comment === "" || comment === undefined)) {
			setCommentsError(true);
			return;
		}
		setLoading(true);
		showHideApprovalModal(false);
		let app_user_id: string = '';
		let app_user_full_name = '';
		// let app_user_first_name: string = '';
		// let app_user_last_name: string = '';
		Utils.StorageService('user_data').then((result) => {
			console.log('Utils', result);
			if (result && result !== undefined) {

				for (const i in result) {
					if (result[i]["key"] === "user_id") {
						app_user_id = result[i]["value"]
					}
					if (result[i]["key"] === "full_name") {
						app_user_full_name = result[i]["value"]
					}
					// if (result[i]["key"] === "first_name") {
					// 	app_user_first_name = result[i]["value"]
					// }
					// if (result[i]["key"] === "last_name") {
					// 	app_user_last_name = result[i]["value"]
					// }
				}
			}
		});
		let commentObject = {

			user_id: app_user_id,
			full_name : app_user_full_name,
			// first_name: app_user_first_name,
			// last_name: app_user_last_name,
			action: withconditions ? "Approved With Conditions" : "Approved",
			content: comment
		};
		const { deletedNodes, newNodes, editedNodes } = getEditedNodesPayload(initialNodes, benefitNodes, editedNodeList, clientData, STATUS_APPROVED, commentObject);

		//console.log("LAST NAME",app_user_last_name);
		let nodes: any = [...editedNodes]
		nodes = nodes.map((node: any) => {
			let newItem = Object.assign({}, node);
			//Add custom meta data for MCT
			if (newItem.code === 'MCT') {
				
				if (!newItem['custom_metadata']) {
					newItem['custom_metadata'] = {};
				}
				if (!newItem['custom_metadata_schema']) {
					newItem['custom_metadata_schema'] = {};
					newItem['custom_metadata_schema']["properties"] = {};
				}
				if (newItem['custom_metadata']['coverage_start_date']) {
					newItem['custom_metadata']['coverage_start_date'] = moment(new Date(proposalInfo.coverage_start_date)).format("YYYY-MM-DDT00:00:00.000") + "Z";
				}
				if (newItem['custom_metadata']['coverage_end_date']) {
					newItem['custom_metadata']['coverage_end_date'] = moment(new Date(proposalInfo.coverage_end_date)).format("YYYY-MM-DDT23:59:59.000") + "Z";

				}
				newItem['custom_metadata_schema']['comments'] = commentObject;
				if (proposalInfo.additional_details.includes("Address") ||
					proposalInfo.additional_details.includes("Contact Person") ||
					proposalInfo.additional_details.includes("Company Name")) {
					let addinfo = {};
					newItem['custom_metadata_schema']["properties"]["additional_info"] = {}
					if (proposalInfo.additional_details.includes("Address")) {
						if (proposalInfo.AddressSelected === 'branch') { addinfo['address'] = proposalInfo.SelectedBranch }
						else {
							addinfo['address'] = proposalInfo.AddressSelected
						}
						newItem['custom_metadata_schema']["properties"]["additional_info"]["address"] = {
							"type": "string"
						}
					}
					if (proposalInfo.additional_details.includes("Contact Person")) {
						addinfo['contact_person'] = proposalInfo.ContactPersonSelected
						if (proposalInfo.ContactPersonSelected !== 'Signatory') {
							addinfo['contact_person_name'] = proposalInfo.ContactPersonName
							newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_name"] = {
								"type": "string"
							}
							if (proposalInfo.ContactPersonNumber && proposalInfo.ContactPersonNumber.trim() !== '') {
								addinfo['contact_person_number'] = proposalInfo.ContactPersonNumber
								newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_number"] = {
									"type": "string"
								}
							}
							if (proposalInfo.ContactPersonEmailAddress && proposalInfo.ContactPersonEmailAddress.trim() !== '') {
								addinfo['contact_person_email_address'] = proposalInfo.ContactPersonEmailAddress
								newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_email_address"] = {
									"type": "string"
								}
							}
							if (proposalInfo.ContactPersonDesignation && proposalInfo.ContactPersonDesignation.trim() !== '') {
								addinfo['contact_person_designation'] = proposalInfo.ContactPersonDesignation
								newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person_designation"] = {
									"type": "string"
								}
							}
						}
						newItem['custom_metadata_schema']["properties"]["additional_info"]["contact_person"] = {
							"type": "string"
						}
					}
					if (proposalInfo.additional_details.includes("Company Name")) {
						addinfo['company_name'] = proposalInfo.CompanyNameSelected
						newItem['custom_metadata_schema']["properties"]["additional_info"]["company_name"] = {
							"type": "string"
						}
					}
					newItem['custom_metadata']['additional_info'] = addinfo;
				}
			}
			return newItem;
		});

		const clientID = props.match.params.client_id;
		const contractID = props.match.params.proposal_id;

		let client = getClientPayload(clientData,proposalInfo);

		if (client && client['sales_channel'] && client['sales_channel']['type']) {
			if (client['sales_channel']['type'] === 'bdo') {
				client['sales_channel']['display_name'] = client['sales_channel']['bdo_name']
			} else if (client['sales_channel']['type'] === 'broker') {
				client['sales_channel']['display_name'] = client['sales_channel']['contact_person']
			} else if (client['sales_channel']['type'] === 'agent') {
				client['sales_channel']['display_name'] = client['sales_channel']['agent_name']
			}
		}

		let data = {
			client: client,
			new_nodes: withconditions ? newNodes : [],
			deleted_nodes: withconditions ? deletedNodes : [],
			//benefit_nodes : benefitNodes,
			// new_nodes: newNodes,
			// deleted_nodes: deletedNodes,
			edited_nodes: editedNodes
		};

		const body = {
			"proposal_approved": true,
			"revisions": withconditions
		}

		if (withconditions === true && client && client.registered_name
			&& client.registered_name !== undefined) {
			body["client_name"] = client.registered_name;
		}

		const files: any = getFiles([], false);
		const filesPayload = new FormData();

		files.map(file => {
			filesPayload.append(`${file.field}`, file.file);
		});
		const files_uploaded: any = getFiles([], true);

		let files_uploaded_payload: any = [];
		files_uploaded.map(file => {
			files_uploaded_payload.push(file.file);
		});
		if (withconditions === true && files.length > 0) {
			fetch(`${API_URL}tmp/upload`, {
				method: 'POST',
				body: filesPayload
			})
				.then(uploadResponse => uploadResponse.json())
				.then(uploadResponse => {
					console.log(uploadResponse);
					if (!uploadResponse.error && !uploadResponse.message) {
						files_uploaded_payload.push.apply(files_uploaded_payload, uploadResponse);
						if (data && data["client"]) {
							data["client"]["files"] = files_uploaded_payload;
						}
						if (data && data.edited_nodes && data.edited_nodes.length > 0
							&& files_uploaded_payload && files_uploaded_payload.length > 0) {
							for (const i in data.edited_nodes) {
								if (data.edited_nodes[i] && data.edited_nodes[i] !== undefined
									&& data.edited_nodes[i]["code"] === 'CT') {
									data.edited_nodes[i]["uploadedComputation"] = files_uploaded_payload ?
										files_uploaded_payload.filter(file => file.field !== undefined && file.field.includes('computation')) : []
									break;
								}
							}
						}
						processApproveProposal(clientID, contractID, data, withconditions, body);
					}
					else {
						console.log('error uploading file for proposal: ', uploadResponse);
						if (uploadResponse && uploadResponse.error && uploadResponse.error.statusCode
							&& uploadResponse.error.statusCode === 500) {
							showFailedModal("File type not supported.", "Approve Proposal with conditions");
						} else if (uploadResponse.message) {
							showFailedModal(uploadResponse.message, "Approve Proposal with conditions");
						} else {
							showFailedModal("An error occurred while saving attached files.", "Approve Proposal with conditions");
						}
					}
				})
				.catch(error => {
					console.log('error uploading file for proposal catch: ', error);
					if (error.message) {
						showFailedModal(error.message, "Approve Proposal with conditions");
					} else {
						showFailedModal("An error occurred while saving attached files.", "Approve Proposal with conditions");
					}
				});
		} else {
			processApproveProposal(clientID, contractID, data, withconditions, body);
		}
	}

